/* General Section Styling */
#skills {
    padding: 50px;
    background-color: #111;
    color: #fff;
    text-align: center;
    min-height: auto; /* Allow dynamic height based on content */
}

  
  #skills h2 {
    font-size: 2.5rem;
    margin-bottom: 30px;
    color: #ffc107;
  }
  
  /* Skills Container */
  .skills-container {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
    align-items: flex-start; /* Prevent content compression */
}
  
  .skills-category {
    background-color: #222;
    border-radius: 10px;
    padding: 20px;
    width: 300px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.4);
    animation: fadeIn 1s ease-in-out;
  }
  
  .skills-category h3 {
    color: #ffc107;
    font-size: 1.5rem;
    margin-bottom: 15px;
  }
  
  /* Skill Bar Styling */
  .skill {
    margin-bottom: 15px;
  }
  
  .skill span {
    display: block;
    font-size: 1rem;
    margin-bottom: 5px;
  }
  
  .progress-bar {
    height: 8px;
    background: #333;
    border-radius: 5px;
    overflow: hidden;
    position: relative;
  }
  
  .progress-bar::after {
    content: "";
    display: block;
    height: 100%;
    background: linear-gradient(90deg, #ffc107, #ff5722);
    width: var(--progress);
    animation: growBar 1s ease-out;
  }
  
  /* Animations */
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(10px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  @keyframes growBar {
    from {
      width: 0%;
    }
    to {
      width: var(--progress);
    }
  }
  
  /* Hover Effect */
  .skills-category:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 15px rgba(255, 193, 7, 0.4);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }
  .skill:hover span::after {
    content: attr(data-tooltip);
    position: absolute;
    top: -25px;
    left: 50%;
    transform: translateX(-50%);
    background: #ffc107;
    color: #111;
    padding: 5px 10px;
    border-radius: 5px;
    font-size: 0.9rem;
    white-space: nowrap;
  }
  
  